import { useState, useEffect } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardContent from '@material-ui/core/CardContent';
import CssBaseline from '@material-ui/core/CssBaseline';
import Slider from '@material-ui/core/Slider'
import Select from '@material-ui/core/Select'
import Grid from '@material-ui/core/Grid';
import MenuItem from '@material-ui/core/MenuItem';
import Box from '@material-ui/core/Box';
import Fastfood from '@material-ui/icons/Fastfood';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';

function milesToMeters(miles) {
  return Math.ceil(miles * 1609.344)
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

function App() {
  const classes = useStyles();

  const [latitude, setLatitude] = useState(0);
  const [longitude, setLongitude] = useState(0);
  const [miles, setMiles] = useState(4);
  const [cuisine, setCuisine] = useState("any");
  const [restaurant, setRestaurant] = useState("");

  useEffect(() => {
    navigator.geolocation.getCurrentPosition((position) => {
      setLatitude(position.coords.latitude);
      setLongitude(position.coords.longitude);
    });
  }, []);

  function handleSearch() {
    const cuisineOrNone = cuisine ? (cuisine !== "any") : ""
    const url = "https://maps.googleapis.com/maps/api/place/nearbysearch/json?";
    const location = `location=${latitude},${longitude}`;
    const radius = `&radius=${milesToMeters(miles)}`;
    const type = `&keyword=${cuisine},restaurant`;
    const key = "&key=AIzaSyBTj4G_GM3mZn6RJ6VzDnK7CtEpJnUGKDc";
    const fullPath = url + location + radius + type + key;
    fetch(fullPath)
      .then(response => response.json())
      .then(result => (result) => {
        const listOfRestaurants = result.results;
        setRestaurant(listOfRestaurants[0]["name"])
      })
      .catch(e => setRestaurant("Error"));
  }

  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <Fastfood />
        </Avatar>
        <Typography component="h1" variant="h5">
          I'm Hangry
        </Typography>
        <Box mb={3}></Box>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Typography gutterBottom>
              Miles Away
            </Typography>
            <Slider
              value={miles}
              onChange={(e, v) => setMiles(v)}
              step={1}
              marks
              min={1}
              max={10}
              valueLabelDisplay="on"
            />
          </Grid>
          <Grid item xs={12}>
            <Typography gutterBottom>
              Cuisine Style
            </Typography>
            <Select
              fullWidth
              value={cuisine}
              onChange={(e) => setCuisine(e.target.value)}
            >
              <MenuItem value={"any"}>Any</MenuItem>
              <MenuItem value={"american"}>American</MenuItem>
              <MenuItem value={"arab"}>Arab</MenuItem>
              <MenuItem value={"brazilian"}>Brazilian</MenuItem>
              <MenuItem value={"chinese"}>Chinese</MenuItem>
              <MenuItem value={"caribbean"}>Caribbean</MenuItem>
              <MenuItem value={"egyptian"}>Egyptian</MenuItem>
              <MenuItem value={"ethiopian"}>Ethiopian</MenuItem>
              <MenuItem value={"french"}>French</MenuItem>
              <MenuItem value={"german"}>German</MenuItem>
              <MenuItem value={"greek"}>Greek</MenuItem>
              <MenuItem value={"indian"}>Indian</MenuItem>
              <MenuItem value={"italian"}>Italian</MenuItem>
              <MenuItem value={"japanese"}>Japanese</MenuItem>
              <MenuItem value={"lebanese"}>Lebanese</MenuItem>
              <MenuItem value={"malaysian"}>Malaysian</MenuItem>
              <MenuItem value={"mexican"}>Mexican</MenuItem>
              <MenuItem value={"persian"}>Persian</MenuItem>
              <MenuItem value={"peruvian"}>Peruvian</MenuItem>
              <MenuItem value={"russian"}>Russian</MenuItem>
              <MenuItem value={"spanish"}>Spanish</MenuItem>
              <MenuItem value={"thai"}>Thai</MenuItem>
              <MenuItem value={"vietnamese"}>Vietnamese</MenuItem>
            </Select>
          </Grid>
        </Grid>
        <Button
          type="submit"
          onClick={handleSearch}
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
        >
          Find Me Food
        </Button>
        <Typography variant="h5" component="h2">
          {restaurant}
        </Typography>
      </div>
    </Container>
  );
}

export default App;
